export default function MyWorkspaceDashboard() {
  return (
    <>
      <div className='card'>
        <div className='card-body'>
          <div className='row clearfix'>
            <div className='col-md-12'>
              <Chart
                type='area'
                height={350}
                options={{
                  chart: {
                    stacked: true,
                    toolbar: {
                      show: true,
                    },
                  },
                  colors: ['#6435c9', '#f66d9b', '#CED4DC'],
                  dataLabels: {
                    enabled: false,
                  },
                  markers: {
                    size: 0,
                    style: 'hollow',
                  },
                  fill: {
                    type: 'gradient',
                    gradient: {
                        opacityFrom: 0.6,
                        opacityTo: 0.8,
                    }
                  },
                  legend: {
                    position: 'bottom',
                    horizontalAlign: 'center',
                    show: true,
                  },
                  xaxis: {
                    type: 'datetime',
                  },
                  grid: {
                    yaxis: {
                      lines: {
                        show: false,
                      }
                    },
                    padding: {
                      top: 20,
                      right: 0,
                      bottom: 0,
                      left: 0
                    },
                  },
                  stroke: {
                    show: true,
                    curve: 'smooth',
                    width: 2,
                  },
                  tooltip: {
                    x: {
                      format: 'dd MMM yyyy'
                    }
                  },
                }}

                series={[
                  {
                    name: 'Income',
                    data: demoData.filter((row) => {
                      return row['type'].toString().toLowerCase() == 'income'
                    }).map((row) => {
                      return [ Date.parse(row['date']), row['amount']]
                    })
                  },
                  {
                    name: 'Outcome',
                    data: demoData.filter((row) => {
                      return row['type'].toString().toLowerCase() == 'outcome'
                    }).map((row) => {
                      return [ Date.parse(row['date']), row['amount']]
                    })
                  }
                ]}
              />
            </div>
          </div>
        </div>
      </div>
    </>
  )
}